.horizontalMenu {
	html, body, iframe, h1, h2, h3, h4, h5, h6 {
	  margin: 0;
	  padding: 0;
	  border: 0;
	  font: inherit;
	  vertical-align: baseline;
	  font-weight: normal;
	  font-size: 16px;
	  line-height: 18px;
	  font-family: Helvetica, sans-serif;
	  -webkit-font-smoothing: subpixel-antialiased;
	  font-smoothing: antialiased;
	  font-smooth: antialiased;
	  -webkit-text-size-adjust: 100%;
	  -ms-text-size-adjust: 100%;
	  -webkit-font-smoothing: subpixel-antialiased;
	  font-smoothing: subpixel-antialiased;
	  font-smooth: subpixel-antialiased;
	}
  
	.cl {
	  clear: both;
	}
  
	img {
	  border: 0 none;
	  max-width: 100%;
	}
  }
  
  object, embed, video {
	border: 0 none;
	max-width: 100%;
  }
  
  .horizontalMenu {
	a:focus {
	  outline: none;
	}
  
	&:before {
	  content: "";
	  display: table;
	}
  
	&:after {
	  content: "";
	  display: table;
	  clear: both;
	}
  }
  
  /* ================== Desktop Main Menu CSS ================== */
  
  .horizontalMenucontainer {
	background-size: cover;
	overflow: hidden;
	background-attachment: fixed;
	background-position: 50% 0;
	background-repeat: no-repeat;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	position: relative;
  }
  
  .horizontal-main {
	width: 100%;
	position: fixed;
  }
  
  .horizontal-mainwrapper {
	margin: 0 auto;
  }
  
  .desktoplogo {
	padding: 0.75rem;
	margin: 0;
	float: left;
  
	img {
	  width: 100%;
	}
  }
  
  .horizontalMenu {
	color: #fff;
	font-size: 14px;
	padding: 0;
	display: block;
  
	> .horizontalMenu-list {
	  text-align: right;
	  margin: 0 auto 0 auto;
	  width: 100%;
	  display: block;
	  padding: 0;
  
	  > li {
		text-align: center;
		display: block;
		padding: 0;
		margin: 0;
		float: right;
  
		> a {
		  display: block;
		  margin: 0px;
		  border-radius: 0px;
		  text-decoration: none;
		  position: relative;
		  color: #3b384a;
		  border-left: 1px solid #e7eaf3;
		  padding: 15px 12px 15px 12px;
		  font-weight: 500;
		  font-size: 13px;
		}
  
		&:last-child > a {
		  border-left: 0px;
		}
  
		a.menuhomeicon {
		  padding-left: 25px;
		  padding-right: 25px;
		}
  
		> a {
		  i {
			display: inline-block;
			font-size: 14px;
			line-height: inherit;
			margin-right: 5px;
			font-size: 15px;
		  }
  
		  .arrow:after {
			border-left: 4px solid rgba(0, 0, 0, 0);
			border-right: 4px solid rgba(0, 0, 0, 0);
			border-top: 4px solid;
			content: "";
			float: right;
			right: 11px;
			height: 0;
			margin: 0 0 0 14px;
			position: absolute;
			text-align: right;
			top: 33px;
			width: 0;
		  }
		}
  
		&.rightmenu {
		  float: right;
  
		  a {
			padding: 0px 30px 0px 20px;
			border-right: none;
  
			i {
			  font-size: 15px;
			}
		  }
  
		  float: right;
  
		  &:before {
			content: "";
			display: table;
		  }
		}
	  }
	}
  }
  
  /* Desktop Search Bar */
  
  .horizontalMenu-list > li.rightmenu:after {
	content: "";
	display: table;
  }
  
  .horizontalMenu > .horizontalMenu-list > li {
	&.rightmenu {
	  &:after {
		clear: both;
	  }
  
	  > .topmenusearch {
		float: right;
		width: 210px;
		height: 39px;
		position: relative;
		margin: 16px 0px 0px 0px;
  
		.searchicon {
		  -webkit-transition: all 0.7s ease 0s;
		  -moz-transition: all 0.7s ease 0s;
		  -o-transition: all 0.7s ease 0s;
		  transition: all 0.7s ease 0s;
		}
  
		input {
		  width: 100%;
		  position: relative;
		  float: right;
		  top: 0;
		  right: 0;
		  bottom: 0;
		  width: 100%;
		  border: 0;
		  padding: 0;
		  margin: 0;
		  text-indent: 15px;
		  height: 39px;
		  z-index: 2;
		  outline: none;
		  color: #333;
		  background-color: #efefef;
		  -webkit-transition: all 0.7s ease 0s;
		  -moz-transition: all 0.7s ease 0s;
		  -o-transition: all 0.7s ease 0s;
		  transition: all 0.7s ease 0s;
		  font-size: 12px;
  
		  &::placeholder {
			color: #a9a9a9;
		  }
  
		  &:focus {
			color: #333;
			width: 230px;
  
			~ {
			  .btnstyle .fa {
				color: #22252f;
				opacity: 1;
			  }
  
			  .searchicon {
				opacity: 1;
				z-index: 3;
				color: #FFFFFF;
			  }
			}
		  }
		}
  
		.btnstyle {
		  top: 0px;
		  position: absolute;
		  right: 0;
		  bottom: 0;
		  width: 40px;
		  line-height: 30px;
		  z-index: 1;
		  cursor: pointer;
		  opacity: 0.3;
		  color: #333;
		  z-index: 1000;
		  background-color: transparent;
		  border: solid 0px;
		  -webkit-transition: all 0.7s ease 0s;
		  -moz-transition: all 0.7s ease 0s;
		  -o-transition: all 0.7s ease 0s;
		  transition: all 0.7s ease 0s;
  
		  .fa {
			line-height: 37px;
			margin: 0;
			padding: 0;
			text-align: center;
		  }
  
		  &:hover .fa {
			opacity: 1;
		  }
		}
	  }
  
	  zoom: 1;
	}
  
	> {
	  ul.sub-menu {
