	border-radius: 0px;
	transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  
	&::-ms-expand {
	  background-color: transparent;
	  border: 0;
	}
  
	height: 38px;
	border-radius: 0.3rem;
  
	&:focus {
	  border-color: #737688;
	  box-shadow: none;
	}
